@import url('https://fonts.googleapis.com/css?family=PT+Sans:400,700');

.btnDeleteForm {
    width: 50px;
    height: 50px;
    position: absolute;
    top: 0;
    right: 0;
    background-image: url(../assets/img/png/exit.png);
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
    background-color: transparent;
    border: none;
    outline: none;
}

.formCheckout {
    background-color: rgba(#000, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 50;
    width: 100%;
    height: 100%;
}

.row {
    position: relative;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    z-index: 100;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 400;
    font-size: $main-font-size;
    color: #fff;
}


.section {
    background-color: #daa520;
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

header,
main,
footer {
    display: block;
    position: relative;
    z-index: 1;
}

header {
    padding: 48px;
}

.errorReq {
    color: red;
} 

header>h3 {
    font-size: 44px;
    line-height: 100%;
    font-weight: 700;
    margin-bottom: 8px;
}

header>h4 {
    font-size: 22px;
    font-weight: 400;
    letter-spacing: 1px;
    margin: 0;
}

main {
    flex: 1;
    padding: 0 48px;
}

footer {
    width: 100%;
    background-color: black;
    padding: 16px;
    align-self: center;
    text-align: center;
    margin-top: 32px;
}

footer a {
    color: #fff;
    font-weight: 700;
    text-decoration: none;
}

footer a:hover {
    text-decoration: underline;
}

form {
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.label {
    color: rgba(226, 227, 232, .75);
    font-size: 16px;
}

small {
    display: none;
    color: red;
}

small.errorOnce {
    margin-top: 2px;
}

.form-item input[type="text"],
.form-item input[type="number"],
.form-item input[type="email"] {
    display: block;
    /* color: red; */
    font-size: 16px;
    width: 100%;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid black;
    padding: 8px 0;
    appearance: none;
    outline: none;
}

.form-item i {
    font-size: 12px;
    color: red;
}

.box-item {
    height: 50px;
    margin-bottom: 16px;
}

.form-item-double {
    display: flex;
}

.form-item-double .form-item {
    flex: 1 1 auto;
}

.form-item-double .form-item:nth-child(1) {
    padding-right: 16px;
}

.form-item-double .form-item:nth-child(2) {
    padding-left: 16px;
}

.form-item-triple {
    display: flex;
    align-items: center;
    padding-top: 6px;
}

.form-item-triple .radio-label {
    flex: 1 1 auto;
    text-align: left;
}

.form-item-triple .radio-label label {
    display: inline-block;
    vertical-align: middle;
}

.form-item-triple .form-item {
    flex: 3 1 auto;
    text-align: center;
    margin: 0;
}

.form-item-triple .form-item label,
.form-item-triple .form-item input[type="radio"] {
    display: inline-block;
    vertical-align: middle;
    margin: 0 4px;
}

::-webkit-input-placeholder {
    /* WebKit, Blink, Edge */
    color: rgba(226, 227, 232, .75);
    font-size: 16px;
}

:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: rgba(226, 227, 232, .75);
    font-size: 16px;
    opacity: 1;
}

::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: rgba(226, 227, 232, .75);
    font-size: 16px;
    opacity: 1;
}

:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: rgba(226, 227, 232, .75);
    font-size: 16px;
}

::-ms-input-placeholder {
    /* Microsoft Edge */
    color: rgba(226, 227, 232, .75);
    font-size: 16px;
}

.submit {
    display: inline-block;
    font-size: 18px;
    font-weight: 700;
    letter-spacing: 1px;
    padding: 8px 48px;
    margin-top: 32px;
    border: 2px solid black;
    border-radius: 20px;
    cursor: pointer;
    transition: all ease 0.2s;
}

.submit:hover {
    background-color: #eda261;
    border: 2px solid #eda261;
}

.wave {
    position: absolute;
    top: -50px;
    left: 50%;
    width: 800px;
    height: 800px;
    margin-top: -600px;
    margin-left: -400px;
    background: black;
    border-radius: 40%;
    animation: shift 20s infinite linear;
    z-index: 0;
}

@keyframes shift {
    from {
        transform: rotate(360deg);
    }
}